import path from "path";
const makeUrl = article => {
  let url = `/article${article.dir
    .split("blog/dist")
    .join("")}/${article.base.split(".json").join("")}`;

  url = url.charAt(url.length - 1) !== "/" ? url : url.slice(0, -1);

  return url;
};
const completeJson = article => {
  return path.join(article.dir, article.base);
};
const filterPosts = summaryJson => {
  return (
    summaryJson &&
    summaryJson.fileMap &&
    Object.keys(summaryJson.fileMap)
      .filter(file => {
        if (file.indexOf("blog/dist/post") === 0) {
          return true;
        }
      })
      .map(file => {
        return summaryJson.fileMap[file];
      })
      .sort((a, b) => {
        const aDate = Date.parse(a.date);
        const bDate = Date.parse(b.date);
        return bDate > aDate ? 1 : bDate < aDate ? -1 : 0;
      })
  );
};
export { makeUrl, filterPosts, completeJson };
